body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Error {
    color: darkred;
    font-weight: 800;
}
.Person {
    border: 1px solid darkmagenta;
    padding: 8px;
    margin: 8px;
    display: flex;
    flex-wrap: wrap;
}

.Person-picture-container {
    border-radius: 50%;
    padding: 6px;
    width: 180px;
    text-align: center;
}

.Person-picture {
    border: 1px solid darkslateblue;
    border-radius: 50%;
    width: 128px;
    height: 128px;
}

.Person-info {
    flex: 1 1;
}

.Person-info-entry {
    display: flex;
    flex-wrap: wrap;
}

.Person-info-subject {
    width: 100%;
    max-width: 300px;
    font-style: italic;
    margin: 4px 0;
}

.Person-info-value {
    width: 100%;
    max-width: 300px;
    font-weight: bold;
}
.Loading {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.Loading-logo {
  -webkit-animation: Loading-logo-spin infinite 20s linear;
          animation: Loading-logo-spin infinite 20s linear;
  height: 40vmin;
}

@-webkit-keyframes Loading-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Loading-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

